import * as React from "react"
import { Link, graphql } from "gatsby"
import Seo from "../components/seo"


const DEFAULT_LANGUAGE = 'en';

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`;
  const language = DEFAULT_LANGUAGE;
  const [isTextShown, setIsTextShown] = React.useState(false);
  const showText = React.useCallback(() => { setIsTextShown(true); }, []);
  const switchText = React.useCallback(() => { setIsTextShown(d => !d); }, []);

  return (
    <main id="homepage" className={isTextShown ? 'text-shown' : ''}>
      <img id="home-logo" src="./img/logo.svg" width={430}
        height={320}
        onClick={switchText}
        alt={siteTitle}
      />
      <section id="home-text">
        <p><b>Almot√°sim</b> is a studio focused on software, art and digital communication.</p>
        <ul>
          <li><Link to={`${language}/projects/`}>Projects</Link></li>
          <li><Link to={`${language}/texts/`}>Texts</Link></li>
          <li><Link to={`${language}/sketches/`}>Sketches</Link></li>
          <li><Link to={`${language}/about/`}>About</Link></li>
        </ul>
      </section>
    </main>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <>
  <Seo title="All posts" />
  <html lang="en" id="almo-homepage" />
</>

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
  }
`
